.App {
  text-align: center;
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.brightness-50{
  filter: brightness(30%)
}
header.masthead {
  /* text-align: center; */
  color: white;
  /* background-image: url("./components/assets/img/home.jpg"); */
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-position: center center;
  background-size: cover;
  background-color: #008080;
  transition: background-color 1000ms linear;
}

header.masthead .intro-text {
  height: 100vh;
  margin-left: 20vw;
  margin-right: 20vw;
  display: flex;
  justify-content:center;
  flex-direction: column;
  text-align: center;
  
}
header.masthead .intro-text .intro-lead-in {
  font-size: 2.2rem;
  font-style: italic;
  line-height: 2.2rem;
  font-family: 'Comfortaa', cursive;
  margin-bottom: 20px;
  /* -webkit-text-stroke-width: 0.05rem;
  -webkit-text-stroke-color: black; */
}
header.masthead .intro-text .intro-subtitle {
  font-size: 3rem;
  line-height: 3rem;
  font-family: 'Comfortaa', cursive;
  margin-bottom: 15px;
  font-weight: bold;
  /* -webkit-text-stroke-width: 0.18rem;
  -webkit-text-stroke-color: black; */
}
header.masthead .intro-text .intro-subtitle .subtitle-text {
  font-size: 3rem;
  line-height: 3rem;
  font-family: 'Comfortaa', cursive;
  margin-bottom: 15px;
  font-weight: normal;
  /* -webkit-text-stroke-width: 0.08rem;
  -webkit-text-stroke-color: black; */
}

header.masthead .intro-text .intro-heading {
  font-size: 5rem;
  font-weight: 700;
  line-height: 5rem;
  margin-bottom: 25px;
  font-family: 'Comfortaa', cursive;
  /* -webkit-text-stroke-width: 0.25rem;
  -webkit-text-stroke-color: black; */
}
.btn {
  font-family: 'Comfortaa', cursive;
  font-weight: 700;
  
}

.btn-x1 {
  position: absolute;
  top: 50%;
  right: 0%;
  transform: translate(-50%,-50%);
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;

}
.btn-x2 {
  position: absolute;
  top: 50%;
  left: 0%;
  transform: translate(50%,-50%);
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}

.btn-primary {
  background-color: rgba(255,255,255,0.13);
  border-color: #000A;
}

.btn-primary:active, .btn-primary:focus, .btn-primary:hover {
  background-color: rgba(255,255,255,0.17) !important;
  border-color: #0007 !important;
}

.btn-primary:active, .btn-primary:focus {
  -webkit-box-shadow: 0 0 0 0.2rem rgba(255, 255, 255, 0.17) !important;
  box-shadow: 0 0 0 0.2rem rgba(255, 255, 255, 0.17) !important;
}
.flex-row{
  display: flex;
  flex-direction: row;
  align-items: center; 
  align-content: center;
  justify-content: center;
}
.flex-column{
  display: flex;
  flex-direction: column;
  align-items: center; 
  align-content: center;
  justify-content: center;
}
img.arrow{
  opacity: 0.4;
  margin-top: 100px;
  margin-bottom: 100px;
}
img.arrow.left{
  transform: scaleX(-1)
}
.disable-text-selection{
  -ms-user-select:none;
  -moz-user-select:none;
  -webkit-user-select:none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
   user-select:none;
}
.icon{
  opacity: 100%;
  -moz-user-select: none;
  -webkit-user-select: none; 
  -ms-user-select:none; 
  user-select:none;
  -o-user-select:none;
}
.icon:hover{
  opacity:70%;
}