header.main-component{
    height: 100vh;
    width: 100%;
    background-color:#F8F6F2!important;
    justify-content: space-evenly;
    display: flex;
    align-items: center;
    flex-direction: row;
    -moz-user-select: none;
    -webkit-user-select: none; 
    -ms-user-select:none; 
    user-select:none;
    -o-user-select:none;
}
img.thumbnail{
    border-radius: 50%;
    border: 7px solid #e9ecef;
}
.main-thread{
    font-family: 'Comfortaa', cursive;
    font-size: 2.2rem;
    line-height: 2.2rem;
    color:#212529;
    text-align: center;
    flex-direction: column;
}
.main-thread .subtitle{
    margin-top:20px;
    font-size: 4rem;
    font-weight: 500;
}
.main-thread .title{
    margin-bottom:50px;
    font-size: 6rem;
    font-weight: bold;
}